<template>
    <div>
        <!-- <van-calendar v-model="show"   /> -->
        <van-calendar type="single" title="课程日期" :poppable="false" :style="{ height: '500px' }" :show-confirm="false" :formatter="formatter"
            @select="handleSelect" v-model="currentDate" :default-date="invalidDate"/>

        <!-- <li style="text-align: center; color: rgb(179, 186, 186);">{{ selectedDate }}</li> -->
        <div class="courseInfo" v-if="selectedDate">

            <li> <span class="info_1">{{ courses[0].time }}</span> <span class="info_2" style="float: right;"> KABUTO</span> </li>
            <li><span class="info_2">{{courses[0].name}}</span></li>
            <li><span class="info_2">张老师</span> <span class="info_2">北大教室</span></li>
            <li><span class="info_2">上课内容:制作机器人小猫</span></li>
        </div>
    </div>
</template>
  
<script >

export default {
    data() {
        return {
            selectedDate: '', //用户选中选中的日期
            currentDate: '', // 当前的日期
            courses: [{name: '少儿编程', time: '9:00-10:30'}], // 课程信息列表
            selectedEvent: '',
            invalidDate: new Date('1970-01-01'), //设置一个不存在的时间来表示当前默认选中的日期这样就看不见了

        };
    },
    methods: {
        formatter(day) {
            const month = day.date.getMonth() + 1;
            const date = day.date.getDate();

            if (month === 7) {
                if (date === 1) {
                    day.bottomInfo = '有课';
                } else if (date === 4) {
                    day.bottomInfo = '有课';
                } else if (date === 11) {
                    day.bottomInfo = '有课';
                }
            }

            return day;
        },

        // 获取选中的时间
        handleSelect(date) {
            const year = date.getFullYear();
            const month = date.getMonth() + 1;
            const day = date.getDate();
            const formattedDate = `${year}年${month}月${day}日`;
            this.selectedDate = formattedDate
            //   console.log(formattedDate);

            // 点击某个日期传递日期发送请求
            this.selectedDate = date;
            this.fetchCourses(date);
            
        },

      
        fetchCourses(date) {
            // 从后端或数据源中获取相应日期的课程信息
            // 示例代码，实际情况下根据您的数据源和后端接口进行相应的数据获取
            // 这里使用 setTimeout 模拟异步请求获取数据
            setTimeout(() => {
                this.courses = [
                    { name: '少儿编程', time: '9:00-10:30'},
                    { name: '少儿积木', time: '14:00-16:00'}
                ];
            }, 500);
        }

    },

};
</script>

<style lang="less" scoped>
* {
    margin: 0;
    padding: 0;
    list-style: none;
}

.courseInfo {
    margin: 20px 10px;
    padding: 10px;
    border-radius: 5px;
    background: rgb(230, 227, 227);

    span {
        color: rgb(119, 116, 116);
    }

    .info_1 {
        color: black;
        font-size: 20px;
    }
}
</style>
  